window.$ = window.jQuery = require('jquery')

require('bootstrap/js/dist/collapse')
$('body').click(function(){
	$('.collapse').collapse('hide')
})
$('body').on('click','.collapse',function(e){
	var theEvent = e || window.event
	theEvent.stopPropagation()
})
