require('bootstrap/js/dist/modal')
require('bootstrap/js/dist/tab')
require('../static/style/main.scss')
require('../static/script/navbar.js')
require('sweetalert')


if(document.getElementById('app')){
	require.ensure([],function(){
		const _ = require('./entranceObject.js')
	},'onlineMediaEditor')
}



